.feedback {
  padding-top: 50px;
  padding-bottom: 60px;
  text-align: center;

		// .feedback__slider

		&__slider {
      position: relative;
      overflow: hidden;
      width: 100%;
		}
}

.swiper-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;

  margin-top: 55px;
  width: 100%;  
}
.swiper-wrapper .swiper-slide {
  display: flex;
  flex-direction: column;  

  min-height: 145px;
  min-width: 381px;
  
  padding: 5px;

  position: relative;
  box-sizing: border-box;
  opacity: 0.3;
}

.slide-img { 
  width: 75px;
  height: 75px;
  
  margin: 0 auto;
  margin-bottom: 10px;
  margin-top: 10px;

  background-image: url("../../img/feed1.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;  
}

.slide-title {
  font-size: 16px;
  line-height: 22px;
  font-weight: 700;
  color: #3a3a3a;

  margin-bottom: 10px;
}

.slide-text {
  font-size: 11px;
  font-weight: 300;
}


.swiper-wrapper .swiper-slide-active {  
  transform: scaleX(1.36) scaleY(1.4);
  min-width: 390px;
  opacity: 1;
  
  transition: transform 0.3s opacity 0.3s;
}

