.footer {
  padding-top: 24px;
  background-color: #25333c;
}

.footer__top {
  padding-bottom: 25px;

  border-bottom: 1px solid #677177;

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.footer__logo {
  margin-right: 43px;

  img {
    width: 68px;
    height: 71px;
  }
  
}

.footer__title {
  margin-right: 270px;

  font-family: Roboto;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  color: #ffffff;
}

.footer__contact {
  margin-right: 18px;

  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.footer__question {
  margin-bottom: 5px;
  color: #fff;
}

.footer__bottom {
  padding: 30px 20px;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-bottom__text {
  opacity: 0.6;
  font-family: Roboto;
  font-size: 13px;
  line-height: 17px;
  font-weight: 300;
  color: #ffffff;
}

.footer-bottom__link {
  font-family: Roboto;
  font-size: 13px;
  line-height: 17px;
  font-weight: 300;
  text-decoration: underline;
  color: #ec644b;
}

.footer-bottom__item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-bottom__img {
  margin-left: 10px;

  display: block;
}