
.questions {
  min-height: 438px;

  background-image: url("./../../img/bg/form_bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  // .questions__wrapper

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    width: 896px;

    margin: 0 auto;
   
    // .questions__wrapper-head

		&-head {
      display: flex;
      flex-direction: column;

      padding-top: 20px;
      padding-bottom: 19px;

      background-color: #25333c;
		}

		// .questions__wrapper-title

		&-title {
      font-size: 19px;      
      font-weight: 700;
      text-transform: uppercase;

      color: #fff;
		}

		// .questions__wrapper-subtitle

		&-subtitle {
      font-size: 14px;      
      font-weight: 400;

      text-transform: uppercase;
      color: #ffffff;
		}

		// .questions__wrapper-bottom

		&-bottom {
      padding: 18px 41px 25px 41px;

      background-color: rgba(255,255,255,0.7);
		}

		// .questions__wrapper-form

		&-form {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;

      textarea {
        width: 100%;
        height: 71px;

        margin-bottom: 18px;
        padding-left: 25px;
        padding-top: 18px;

        border-radius: 8px;
        box-sizing: border-box;
      }
		}

  }
  
  // .questions__form-group

		&__form-group {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;

      input {
        width: 325px;

        border-radius: 33px;

        padding-left: 27px;
        padding-top: 18px;
        padding-bottom: 18px;
        margin-right: 6px;
        margin-bottom: 8px;

        &:last-child {
          margin-right: 0;
        }
      }
		}
		
}

