@charset "UTF-8";

/**
 * Основные стили
 **/

html {
	font-size: 16px;
	color: $black;
}

body {
	font-family: 'Roboto', 'Arial', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	hyphens: auto;
	overflow-wrap: break-word;
	word-wrap: break-word;
}

.container {
	width: 100%;
	max-width: 1170px;
	margin: 0 auto;
	padding: 0 15px;

	&--feedback {
		max-width: 1412px;
	}
}

.page {
	display: flex;
    flex-direction: column;
    min-height: 100vh;

    overflow: hidden;
}

.wrap {
	@include min-max(320px, 1170px, 20px);
}

// font-family: 'Roboto Condensed', sans-serif;


h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 0;
}

p {
    margin: 0 0 10px;
}

ul,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

.img {
    display: block;
    max-width: 100%;
    height: auto;
}

.text-center {
    text-align: center;
}

.rel {
    position: relative;
}

.section__title {
	font-size: 26px;
	line-height: 23px;
	font-weight: 700;

	text-transform: uppercase;
	color: #2e2e2e;
	text-align: center;

	margin-bottom: 30px;

	// .section__title--white

	&--white{
		margin-bottom: 40px;
		position: relative;

		&:after {
			content: "";
			display: block;
			width: 148px;
			height: 1px;

			background-color: $primary;

			position: absolute;
			bottom: -100%;
			left: 50%;

			transform: translateX(-50%);
		}
	}

	// .section__title--order

	&--order {
		font-family: Roboto;
		font-size: 28px;
		line-height: 23px;
		font-weight: 400;
		color: #3a3a3a;

		text-transform: none;
		margin-bottom: 71px;
	}

}

.section__subtitle {
	display: block;
    max-width: 879px;
    margin: 0 auto;
    margin-bottom: 47px;

	font-size: 21px;
    line-height: 24px;
    font-weight: 400;

    color: #2e2e2e;
    text-align: center;
}

.service {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	justify-content: center;

	min-height: 342px;

	position: relative;
	z-index: 0;

		&::after {
		content: "";
		width: 100%;
		height: 100%;        

		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		z-index: -1;

		background-color: rgba(0,0,0,0.6);
		}
		
		// service__img

		&__img {
			display: flex;
      align-items: center;
      justify-content: center;
      width: 102px;
      height: 102px;

      margin: 0 auto;
      margin-bottom: 20px;

      border: 1px solid $white;
      border-radius: 100%;
		}

		// service__title

		&__title {
			margin-bottom: 22px;

      font-size: 26px;
      line-height: 23px;
      font-weight: 700;

			color: #ffffff;			
		}

		// service__text

		&__text {
			font-size: 19px;
      line-height: 27px;
      font-weight: 300;

      color: #ffffff;
		}
}