.work {
    padding-top: 50px;
    padding-bottom: 55px;

    text-align: center;
		

		// .work__list

		&__list {
            display: flex;
            justify-content: space-between;
		}

		// .work__item

		&__item {
            max-width: 360px;
            box-sizing: border-box;
		}

		// .work__img

		&__img {
            margin-bottom: 22px;
		}

		// .work__content

		&__content {
		}

		// .work__content-title

		&__content-title {
            margin-bottom: 10px;

            font-size: 18px;
            line-height: 22px;
            font-weight: 700;

            color: $primary;
		}

		// .work__content-text

		&__content-text {
            font-size: 14px;
            font-weight: 400;

            color: $text-color;
            text-align: justify;
		}
}

