.header {
//     height: 100vh;
    min-height: 680px;

    position: relative;
    z-index: 0;

    padding-top: 23px;
    padding-bottom: 82px;

    background-image: url('../../img/bg/bg1.jpg');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;

    text-align: center;
    color: #fff;

    &::after {
        content: "";
        width: 100%;
        height: 100%;        

        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        z-index: -1;

        background-color: rgba(0,0,0,0.8);
    }

		// .header__top

		&__top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;

            margin-bottom: 43px;
		}

		// .header__logo

		&__logo {
		}

		// .header__menu

		&__menu {
            display: flex;
            list-style-type: none;
		}

		// .header__item

		&__item {
            padding: 3px 10px;
            border-right: 1px dotted #fff;
            &:last-child{
                border-right: none;
            }            
		}

		// .header__link

		&__link {
            padding-bottom: 5px;

            font-size: 15px;
            line-height: 16px;
            font-weight: 400;
            color: $white;
            text-decoration: none; 
            
            transition: all 0.3s linear;
            
            &:hover {
                color: $primary;
                border-bottom: 1px solid $primary;
            }
		}

		// .header__contact

		&__contact {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
		}

		// .header__question

		&__question {
            font-size: 14px;
            line-height: 20px;
            font-weight: 300;            
		}

		// .header__phone

		&__phone {
            font-size: 19px;
            line-height: 19px;
            font-weight: 700;
            color: #ffffff;
            text-decoration: none;
		}		

		// .header__text

		&__text {
            font-size: 24px;
            line-height: 1.2;
            font-weight: 300;

            margin-bottom: 26px;
		}

		// .header__title

		&__title {
            font-family: 'Roboto Condensed', sans-serif;
            font-size: 72px;
            line-height: 1.2;
            font-weight: 700;

            text-transform: uppercase;
            color: $primary;

            margin-bottom: 5px;
		}

		// .header__subtitle

		&__subtitle {
            font-family: 'Roboto Condensed', sans-serif;
            font-size: 42px;
            line-height: 1.2;
            font-weight: 400;
            text-transform: uppercase;

            margin-bottom: 40px;
		}

		// .header__center

		&__center {
            display: flex;
            align-items: center;
            justify-content: space-between;             

            width: 909px;
            margin: 0 auto;
            margin-bottom: 40px;
            
            &-left {
                display: block;
                width: 530px;
                height: 2px;
                background-color: #fff;
            }
            
            &-round {
                display: block;
                width: 10px;
                height: 10px;
                margin-right: 15px;
                margin-left: 15px;

                background-color: #fff;
                border-radius: 100%;
            }
            &-right {
                display: block;
                width: 530px;
                height: 2px;
                background-color: #fff;
            }           
		}

		// .header__features

		&__features {
            display: flex;
            justify-content: space-between;
            margin-bottom: 45px;
		}

		// .header__features-item

		&__features-item {
            display: block;
            width: 260px;
            height: 150px;
		}

		// .header__features-img

		&__features-img {
            width: 67px;
            height: 63px;

            margin-bottom: 13px;
		}

		// .header__features-text

		&__features-text {
            font-size: 16px;
            line-height: 22px;
            font-weight: 300;

		}
}
