.price {
    padding-top: 53px;
    padding-bottom: 28px;

    text-align: center;

		// .price__list

		&__list {
            display: flex;
            align-items: center;
            justify-content: space-between;
		}

		// .price__item

		&__item {
            width: 265px;
            height: 355px;

            border: 1.0px solid #e9e9e9;
		}

		// .price__item-head

		&__item-head {
            display: flex;
            align-items: center;
            justify-content: center;

            height: 61px;
            margin-bottom: 14px;
            background-color: #25333c;

            color: $white;
		}

		// .price__item-img

		&__item-img {
            width: 61px;
            height: 57px;
            margin-bottom: 12px;
		}

		// .price__item-text

		&__item-text {
            font-size: 14px;
            line-height: 23px;
            font-weight: 400;
            text-transform: uppercase;

            color: #2e2e2e;
		}

		// .price__item-title

		&__item-title {
            margin-bottom: 20px;

            font-size: 28px;
            line-height: 30px;
            text-transform: none;

            color: $primary;
		}

		// .price__item-descr

		&__item-descr {
            margin-bottom: 25px;

            font-size: 14px;
            line-height: 20px;
            font-weight: 300;
            color: #2e2e2e;
		}
}
