.order {
    padding-top: 25px;
    padding-bottom: 58px;

    text-align: center;
		// .order__wrapper

		&__wrapper {
      border: 1px solid #898989;      

      position: relative;
		}

		// .order__img

		&__img {
      padding: 10px;      

      background-color: #fff;            

      position: absolute;
      top: -2%;
      left: 50%;

      transform: translate(-50%, -50%);
		}

		// .order__img-inner

		&__img-inner {
      display: flex;
      align-items: center;
      justify-content: center;

      width: 102px;
      height: 102px;

      border: 1px solid #ec644b;
      border-radius: 100%;
		}
}

.order__form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  height: 166px;

  position: relative;

  &::after {
    content: "";
    display: block;

    position: absolute;
    left: 50%;
    bottom: -35%;
        

    width: 180px;
    height: 60px;

    background-color: #fff;
    transform: translate(-50%, -50%);

    }

  // .order__form-group
  
  &-group {
    display: flex;
    align-items: center;
    justify-content: center;

    input {
      width: 230px;
      margin-right: 5px;
      padding: 10px 30px;
      border-radius: 33px;
    }
  }

  
}


