.map {
  width: 100%;
  min-height: 600px;

  background-image: url("../../img/bg/map.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; 
  
  position: relative;
}

.marker {
  width: 73px;
  height: 73px;

  position: absolute;
  left: 50%;
  top: 50%;

  transform: translate(-50%, -50%);

  background-image: url("../../img/layers.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.tip {
  position: absolute;
  left: 54%;
  top: 35%;

  padding: 20px 30px;
  padding-right: 55px;

  background-color: #fff;  
}

.tip::before {
  content: "";

  position: absolute;
  left: 0;
  top: 50%;

  width: 0;
  height: 0;
  border-style: solid;
  border-width: 25px 30px 25px 0;
  border-color: transparent #fff transparent transparent;

  transform: translate(-100%,-50%);
}

.tip__title {
  margin-bottom: 5px;

  font-family: Roboto;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  color: $primary;
}

.tip__address {
  margin-bottom: 5px;

  font-size: 14px;
  font-weight: 300;
  line-height: 22px;
  color: $text-color;

  a {
    text-decoration: none;
  }
}

.tip__email {
  font-size: 14px;
  font-weight: 300;
  color: $primary;
  text-decoration: underline;
}