.button {
    width: 160px;
    height: 45px;


    background-color: $primary;
    border-radius: 50px;
    border: none;

    font-size: 12px;
    line-height: 1.2;
    font-weight: 400;

    text-transform: uppercase;
    color: #ffffff;

    cursor: pointer;

    &:hover {
                box-shadow: 3px 3px 20px $primary;
                border: 2px solid #fff;
            }
    &--big {
        width: 230px;
        height: 57px;

        font-size: 15px;
        line-height: 17px;
        font-weight: 700;        
    }

    &--price {
        padding: 16px 45px;
        text-decoration: none;

        &:hover {
            border: none;
            background-color: #dc5238;
        }
    }

    &--order {
        position: absolute;
        left: 50%;
        bottom: -27%;
        z-index: 10;

        transform: translate(-50%, -50%);

        
    }

    &--questions {
        width: 180px;
        height: 55px;

        font-size: 12px;
        line-height: 22px;
        font-weight: 400;
        text-transform: uppercase;
        color: #ffffff;
    }
}
