.benefits {
    min-height: 590px;
	padding: 55px 0px 50px 0px;

	background-color: #f3f3f3;

    text-align: center;    

		// .benefits__list

		&__list {
            display: flex;
            align-items: center;
            flex-wrap: wrap;
		}

		// .benefits__item

		&__item {
            display: flex;
			width: 50%;
			margin-bottom: 36px;
			
		}

		// .benefits__img

		&__img {
			width: 62px;
			height: 62px;
			margin-right: 17px;
        }
        
        // benefits__content

        &__content {
			max-width: 447px;
			text-align: left;
        }

		// .benefits__subtitle

		&__subtitle {
			margin-bottom: 12px;

			font-size: 16px;
			line-height: 22px;
			font-weight: 700;

			color: $primary;
		}

		// .benefits__text

		&__text {
			font-size: 14px;
			font-weight: 400;
			line-height: 1.7;
			
			color: $text-color;
		}
}

