.scheme {
  padding-top: 51px;
  padding-bottom: 46px;
  text-align: center;
		// .scheme__wrapper

		&__wrapper {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-wrap: wrap;
		}

		// .scheme__item

		&__item {
			width: 257px;
			height: 145px;

			margin: 15px;
		}

		// .scheme__item-img

		&__item-img {			
			height: 55px;

			margin-bottom: 10px;
		}

		// .scheme__item-title

		&__item-title {
			margin-bottom: 5px;

      font-size: 16px;
      line-height: 20px;
      font-weight: 700;
      color: #ec644b;
		}

		// .scheme__title-subtitle

		&__title-subtitle {
      font-size: 14px;
      font-weight: 300;
      color: #3a3a3a;
		}
}

